	module.exports = {"data":{"post":{"html":"<p>Where was I 25 years ago as the Berlin Wall fell? I must confess, I don't remember exactly. If I had known the news would radically change our lives, I might have paid more attention.</p>\n<p>We didn't realize it at the time, but our lives in November 1989 were \"in between\". In between jobs, in between continents, in between everything. We had just moved to Arizona, following her old boss who didn't have a job for her yet. I was working temp jobs hoping to find something permanent (one of them eventually did). We thought Arizona would become our home. We were in love with the desert, the sun, the lifestyle, everything.</p>\n<p>We had, however, picked a horrible time to move to Arizona. The savings and loan crisis had broken out, and the economy was already stagnant. And in the weeks and months that followed, the soon-to-be-reunified Germany became more attractive to us. We thought we could work there for a couple years and come back when the job climate in Arizona was better.</p>\n<p>But where was I when I heard the news? Today I have no idea. In my car listening to NPR? At home watching CNN? With my wife when she got a call from her Hungarian friend Gabi? I just know that less than 10 months later, we were here.</p>","fields":{"title":"Where Were You?","date":"09 Nov 2014","url":"https://papascott.de/2014-11-09-where-were-you/"}}},"pathContext":{"slug":"/2014-11-09-where-were-you/","prev":{"fields":{"title":"Thank you to everyone for everything","date":"11 Oct 2014","slug":"/2014-10-11-thank-you-to-everyone-for-everything/"}},"next":{"fields":{"title":"How I Punked Facebook","date":"10 Feb 2015","slug":"/2015-02-10-how-i-punked-facebook/"}}}}

